// Module：出库信息
<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <!-- 单据类型 -->
        <el-form-item prop="billType">
          <el-select
            v-model="query.billType"
            filterable
            clearable
            placeholder="单据类型"
            style="width: 178px;"
            class="filter-item"
          >
            <el-option
              v-for="(item,index) in dict['wms_out_warehouse_bill_type']"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <!-- 单据状态 -->
        <el-form-item prop="status">
          <el-select
            v-model="query.status"
            filterable
            clearable
            placeholder="单据状态"
            style="width: 178px;"
            class="filter-item"
          >
            <el-option
              v-for="(item,index) in [{label: '未完成', value: 1},{label: '部分完成', value: 2},{label: '已完成', value: 3}]"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <!-- 创建人查询 -->
        <el-form-item prop="billCreateUser">
          <el-input
            v-model.trim="query.billCreateUser"
            clearable
            placeholder="创建人"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <!-- 单号查询 -->
        <el-form-item prop="billNumber">
          <el-input
            v-model.trim="query.billNumber"
            clearable
            placeholder="单号"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <!-- 领料人 -->
        <el-form-item prop="materialHandlerName">
          <el-input
            v-model.trim="query.materialHandlerName"
            clearable
            placeholder="领料人"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <!-- 用料单位 -->
        <el-form-item prop="materialsUnit">
          <el-input
            v-model.trim="query.materialsUnit"
            clearable
            placeholder="用料单位"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <!-- 开始结束日期 -->
        <el-form-item prop="startTime">
          <el-date-picker
            v-model="query.startTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始日期"
            @change="query.endTime = ''"
          />
        </el-form-item>
        <el-form-item prop="endTime">
          <el-date-picker
            v-model="query.endTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束日期"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
          />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="145"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleDetail(scope.row)">详情</el-button>
          <el-button type="primary" :disabled="scope.row.status === 3" @click="handleOutWarehouse(scope.row)">出库</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getDeleteApi } from '@/apis/modules/base/baseSupplier'
import tableHeadSet from '@/mixins/tableHeadSet'
import { getListApi } from '@/apis/modules/out-bound/outbound-information'
import { mapGetters } from 'vuex'
export const statusChartMap = {
  1: '未完成',
  2: '部分完成',
  3: '已完成'
}

export default {
  mixins: [paginationPage, tableHeadSet],
  dicts: ['wms_out_warehouse_bill_type'],
  data() {
    return {
      title: '入库信息',
      loading: false,
      query: {
        billType: '', // 单据类型
        materialsUnit: '', // 用料单位
        billCreateUser: '', // 创建人
        materialHandlerName: '', // 领料人
        billNumber: '', // 单号
        startTime: '', // 开始日期
        endTime: '', // 结束日期
        status: '' // 单据状态
      },
      tableData: [],
      columnList: [
        {
          module: 'status',
          prop: 'billType_dictText',
          label: '单据类型',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (prop) => {
            return prop
          },
          headerAlign: 'center',
          statusFormatter: (scope) => {
            const typeMap = {
              '1': 'success',
              '2': 'primary',
              '3': 'warning',
              '4': 'info'
            }
            return scope.billType ? typeMap[scope.billType] : ''
          }
        },
        {
          module: 'word',
          prop: 'billNumber',
          label: '单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'date',
          label: '日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialsUnit',
          label: '用料单位',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialHandlerName',
          label: '领料人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'stockOutNum',
          label: '出库单数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '86',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            const typeMap = {
              '1': 'success',
              '2': 'primary',
              '3': 'warning'
            }
            return row.status ? typeMap[row.status] : ''
          }
        }
      ],
      columnSets: [{
        module: 'status',
        prop: 'billType_dictText',
        label: '单据类型',
        formatter: (prop) => {
          return prop
        },
        statusFormatter: (scope) => {
          const typeMap = {
            '1': 'success',
            '2': 'primary',
            '3': 'warning',
            '4': 'info'
          }
          return scope.billType ? typeMap[scope.billType] : ''
        }
      }, {
        module: 'status',
        prop: 'status',
        label: '状态',
        formatter: (status) => {
          return statusChartMap[status]
        },
        statusFormatter: (row) => {
          const typeMap = {
            '1': 'success',
            '2': 'primary',
            '3': 'warning'
          }
          return row.status ? typeMap[row.status] : ''
        }
      }],
      dialogForm: {},
      supplierPullList: [],
      dialogType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    },
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  activated() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query } : {
        ...this.query,
        page: {
          ...this.pageInfo
        },
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 批量删除
    handleDelete(params, item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
        const ids = params || this.$refs.baseTableRef.tableSelectionList.map(i => i.id)
        getDeleteApi({
          id: ids[0],
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 详情
    handleDetail(item) {
      this.$router.push({
        name: 'OutBoundInfoDetail',
        params: {
          billNumber: item.billNumber, // 单据编号
          billType: item.billType // 单据类型
        }
      })
    },
    // 出库
    handleOutWarehouse(item) {
      this.$router.push({
        name: 'OutBoundInfoWarehouseOut',
        params: {
          billNumber: item.billNumber, // 单据编号
          billType: item.billType // 单据类型
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
